import { h } from "@stencil/core";
export class RaulContainerTitle {
    render() {
        return (h("slot", null));
    }
    static get is() { return "raul-container-title"; }
    static get originalStyleUrls() { return {
        "$": ["raul-container-title.scss"]
    }; }
    static get styleUrls() { return {
        "$": ["raul-container-title.css"]
    }; }
}
